// Base URL
const base_url = "https://api.themoviedb.org/3/movie/";
const api_key = "api_key=f6bbff71f547206eec4072098c9cfe17";

export const poster_url = () => "https://image.tmdb.org/t/p/w1280";
export const backdrops_url = () =>
  "https://www.themoviedb.org/t/p/w1066_and_h600_bestv2";

export const upcomingMoviesURL = () =>
  `${base_url}upcoming?${api_key}&language=en-US`;

export const popularMoviesURL = () =>
  `${base_url}popular?${api_key}&language=en-US&page=1`;

export const nowPlayingMoviesURL = () =>
  `${base_url}now_playing?${api_key}&language=en-US&page=1`;

// Movie details
export const movieDetailsURL = (movie_id) =>
  `${base_url}${movie_id}?${api_key}&language=en-US`;

// Movie video
export const movieBackdropsURL = (movie_id) =>
  `${base_url}${movie_id}/images?${api_key}`;
